import React, { Component } from "react";
import { SOCIAL_WALL_URL } from "../studioPreview/actions/api";

class SocialWall extends Component {
  constructor(props) {
    super(props);

    this.state = {
      loading: false,
    };
  }

  componentWillMount() {
    ///css and js remove 
    ["display-0", "display-1"].map((itm) => {
      this.onLoadRemoveCss(`css-${itm}`)
      this.onLoadRemoveScript(`js-${itm}`)
    })

    document.querySelectorAll("style").forEach(style => style.disabled = true);

    ///body margin set
    const body = document.getElementsByTagName("body");
    if (body) body[0].style.margin = 0;
  }
  onLoadRemoveCss = (id) => {
    try {
      const scriptList = document.querySelectorAll("link[type='text/css']")
      const convertedNodeList = Array.from(scriptList)
      const testScript = convertedNodeList.find(script => script.id === id)
      testScript.parentNode.removeChild(testScript)
    }
    catch (ex) {

    }
  }
  onLoadRemoveScript = (id) => {
    try {
      const scriptList = document.querySelectorAll("script[type='text/javascript']")
      const convertedNodeList = Array.from(scriptList)
      const testScript = convertedNodeList.find(script => script.id === id)
      testScript.parentNode.removeChild(testScript)
    }
    catch (ex) {

    }
  }

  componentDidMount() {
    window.wall_Id = this.props.wallId;
    window.wall_id = this.props.wallId;
    var url = SOCIAL_WALL_URL;

    fetch(url)
      .then((response) => response.json())
      .then((json) => {
        const { id, script, css } = json;

        // let myScript = document.createElement("div");
        // myScript.setAttribute("id", id);

        // document.getElementById("taggbox_main").appendChild(myScript);

        let myCss = document.createElement("link");
        myCss.setAttribute("type", "text/css");
        myCss.setAttribute("rel", "stylesheet");
        myCss.setAttribute("href", css);
        document.head.appendChild(myCss);

        let myreact = document.createElement("script");
        myreact.setAttribute("src", script);
        document.body.appendChild(myreact);
      });
  }

  render() {
    return <div id={`taggbox_main`}></div>;
  }
}

export default SocialWall;
